.footer {
    border-top: 8px solid #222;
    padding: 70px 45px;
    /* margin: 0 20px; */

}

.footer .footer-ctnt {
    width: 960px;
    margin: 0 auto;
}

ul {
    padding-left: 10px;
}

.footer ul li {
    list-style: none;
    opacity: 0.5;
    margin-bottom: 16px;
    font-size: 13px;
}

.footer ul a,
.footer h1 a {
    text-decoration: none;
    color: white;

}

.footer ul a:hover {
    text-decoration: underline;
    color: white;
}


.footer h1 {
    color: #888888;

    /* opacity: 0.5; */
    font-size: 16px;
}

.footer h1:hover {
    opacity: 1;
}

.footer h1 a {
    color: #fff;
    opacity: 0.5;
}

.footer h1 a:hover {
    opacity: 1;
}


.footer .contact-us {
    margin-bottom: 10px;
    padding: 20px 10px 0;
}

.footer .contact-us h2 a {
    text-decoration: none;
    color: white;
    opacity: 0.5;
    font-size: 13px;
}

.footer .contact-us h2 a:hover {
    text-decoration: underline;
    color: white;
}

.footer .nflx-location h2 {
    color: white;
    font-size: 13px;
    opacity: 0.5;
    padding: 20px 10px;

}

@media (min-width: 768px) {
    .footer__links {

        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    .footer h1 {
        text-align: center;
    }
}